/* eslint-disable max-lines */
import React, { useEffect, useMemo, useState } from 'react';
import { Button, Col, Row } from 'reactstrap';
import { useDispatch } from 'react-redux';
import { generatePath, match } from 'react-router-dom';
// import { Cell } from 'react-table';
import { push, replace } from 'connected-react-router';
import { get, toNumber } from 'lodash';
import qs from 'query-string';
import DataTable from '../../components/DataTable';
// import { numberFormat } from '../../utils/common';
// import parse_query_string from '../../utils/parse_query_string';
import routesMap from '../../utils/routesMap';
import { deleteRole, getAllRoles } from '../../redux/Roles/action';
import replaceUrlParam from '../../utils/replaceUrlParam';
import Pagination from '../../components/Pagination';
import ModalRole from '../../components/Modal/ModalRole';
import { toast } from 'react-toastify';
import ModalPopupConfirm from '../../components/Modal/ModalPopupConfirm';
// eslint-disable-next-line import/imports-first

interface Props {
  match: match;
}

// eslint-disable-next-line max-lines-per-function
const RolesScreen: React.FC<Props> = (props: Props): JSX.Element => {
  const dispatch = useDispatch();
  const params = qs.parse(get(props, 'location.search'));
  const pageNumberUrl = get(params, "pageNumber");
  const pageitemsUrl = get(params, "pageitems");
  const [totalPage, setTotalPage] = useState<number>(1);
  const [data, setData] = useState<any>([]);
  const [pageNumber, setPageNumber] = useState<number>(pageNumberUrl ? toNumber(pageNumberUrl) : 1);
  const [pageSize, setPageSize] = useState<number>(pageitemsUrl ? toNumber(pageitemsUrl) : 20);
  const [showModalTaoMoi, setShowModalTaoMoi] = useState<boolean>(false);
  const [currentRow, setCurrentRow] = useState<any>({});
  // const idDonHang = get(props, 'match.params.idDonHang');
  useEffect(() => {
    dispatch(
      getAllRoles({
        pageNumber: pageNumber,
        pageSize: pageSize
      },
        (data: any) => {
          if (data.list.length >= 0) {
            setData(data.list)
            setTotalPage(toNumber(data.total_pages))
          }
        },
        () => { }
      )
    )
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleRedirectDetail = (item: any): void => {
    dispatch(
      push(
        generatePath(routesMap.THONG_TIN_CHI_TIET_ROLE, {
          idRole: item.id,
        }),
      ),
    );
  };

  function handleEditClick(
    row: any,
  ): (event: React.MouseEvent) => void {
    return (): void => {
      setCurrentRow(row)
      setShowModalTaoMoi(!showModalTaoMoi);
    };
  }

  function handleRemoveClick(
    row: any,
  ): (event: React.MouseEvent) => void {
    return (): void => {
      dispatch(
        deleteRole({
          idRole: get(row, "original.id")
        },
          (data: any) => {
            toast.success(`Xoá quyền ${get(row, "original.roleName", "")} thành công`)
            dispatch(
              getAllRoles({
                pageNumber: pageNumber,
                pageSize: pageSize
              },
                (data: any) => {
                  if (data.list.length >= 0) {
                    setData(data.list)
                    setTotalPage(toNumber(data.total_pages))
                  }
                },
                () => { }
              )
            )
          },
          () => { }
        )
      )
    };
  }

  const pushSearchConditionToUrl = (pageNumber: number): void => {
    const url = replaceUrlParam(window.location.pathname + window.location.search, {
      pageNumber: pageNumber,
    });
    dispatch(replace(generatePath(url)));
  };

  const onPaginationChange = (selectedItem: { selected: number }): void => {
    setPageNumber(selectedItem.selected + 1)
    pushSearchConditionToUrl(selectedItem.selected + 1);
  };

  const toggleModalTaoMoi = (): void => {
    if (showModalTaoMoi === true) setCurrentRow({})
    setShowModalTaoMoi(!showModalTaoMoi);
  };

  const getListRole = (): void => {
    dispatch(
      getAllRoles({
        pageNumber: pageNumber,
        pageSize: pageSize
      },
        (data: any) => {
          if (data.list.length >= 0) {
            setData(data.list)
            setTotalPage(toNumber(data.total_pages))
          }
        },
        () => { }
      )
    )
    toggleModalTaoMoi()
  };

  const columns = useMemo(
    // eslint-disable-next-line max-lines-per-function
    () => [
      {
        Header: 'ID',
        accessor: 'id',
      },
      {
        Header: 'Tên quyền',
        accessor: 'roleName',
      },
      {
        Header: 'Quản trị',
        accessor: '',
        Cell: ({ row }: any): JSX.Element => (
          <Col>
            <Button className="ml-2" color="primary"
              onClick={handleEditClick(row)}
            >
              <i className="fa fa-pencil mr-2" />
              {'Sửa'}
            </Button>
            {/* <Button className="ml-2" color="primary"
              onClick={handleRemoveClick(row)}
            >
              <i className="fa fa-trash mr-2" />
              {'Xoá'}
            </Button> */}
            <ModalPopupConfirm classNameBtn="ml-2"
              buttonLabel={
                <>
                  <i className="fa fa-trash mr-2" />
                  {'Xoá'}
                </>
              } 
              handleDoSomething={handleRemoveClick(row)} />
          </Col>
        ),
      },
    ],
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [],
  );

  function renderTable(): JSX.Element {
    return (
      <Row className="sipTableContainer sipTableRowClickable">
        <DataTable disableOnClickCellEnd={true} columns={columns} data={data} onRowClick={handleRedirectDetail} />
      </Row>
    );
  }
  return (
    <>
      <Row className="mb-3 sipTitleContainer">
        <h1 className="sipTitle">{'Thông tin quyền'}</h1>
        {/* <RootTypeahead
          id="location"
          //  labelKey={labelKeyLocation}
          options={listLocation}
          placeholder="Chọn đơn vị"
        // onInputChange={setKeywords}
        //  onChange={handleSelectedLocation}
        //  selected={selectedLocation}
        >
          <span
            style={{
              position: 'absolute',
              right: '8px',
              top: '10px',
            }}
            className="fa fa-caret-down"
          />
        </RootTypeahead> */}
        <div className="sipTitleRightBlock">
          <Button className="ml-2" color="primary"
            onClick={toggleModalTaoMoi}
          >
            <i className="fa fa-pencil mr-2" />
            {'Thêm mới'}
          </Button>
          {/* <Button className="ml-2" color="primary" >
            <i className="fa fa-pencil mr-2" />
            {'Sửa'}
          </Button> */}
          {/* <Button className="ml-2" color="primary">
            <img src={'../../assets/img/icon/iconInBuuGui.svg'} alt="VTPostek" />
            {'In bưu gửi'}
          </Button> */}
        </div>
      </Row>
      <h1 className="sipTitle">{'Danh sách quyền'}</h1>
      {renderTable()}
      <Pagination
        pageRangeDisplayed={2}
        marginPagesDisplayed={2}
        pageCount={totalPage}
        onThisPaginationChange={onPaginationChange}
        setPageSize={setPageSize}
      />
      <ModalRole
        visible={showModalTaoMoi}
        onSuccessCreated={getListRole}
        onHide={toggleModalTaoMoi}
        modalTitle={'Tạo quyền'}
        item={currentRow}
      />
    </>
    //   ) : (
    //     <Fade in={true} timeout={1000}>
    //       <Row className="mb-3 sipTitleContainer">
    //         {/* <h1 className="sipTitle">
    //           <ButtonGoBack />
    //           {t('Quay lại')}
    //         </h1> */}
    //       </Row>
    //       <div className="row mb-5" />
    //       <h3 className="text-center">{'Không tìm thấy thông tin phiếu gửi!'}</h3>
    //     </Fade>
  );
};

export default RolesScreen;
