import React, { FormEvent, useEffect, useState } from 'react';
import { Button, Modal, ModalHeader, ModalFooter, ModalBody, FormGroup, Label, CustomInput, Input, Col, Row } from 'reactstrap';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';
import { toast } from 'react-toastify';
import { createDetailReport } from '../../redux/BaoCaoLamViec/action';
import { filter, get, map } from 'lodash';
import { createRole, getAllSubRoles, getDetailRole, updateRole } from '../../redux/Roles/action';

interface Props {
    onHide: () => void;
    onSuccessCreated: () => void;
    visible: boolean;
    modalTitle: string;
    item?: any
}

// eslint-disable-next-line max-lines-per-function
const ModalRole: React.FC<Props> = (props: Props): JSX.Element => {
    const { t } = useTranslation();
    const dispatch = useDispatch();
    const { onHide, onSuccessCreated, visible, modalTitle, item } = props;

    const [data, setData] = useState<any>([]);
    const [roleName, setRoleName] = useState<string>('');

    useEffect((): void => {
        if (get(item, "original.id")) {

            setRoleName(get(item, "original.roleName", ""))
            dispatch(
                getDetailRole({ id: get(item, "original.id"), pageNumber: 1, pageSize: 1000 },
                    (data1: any) => {
                        if (data1.list.length >= 0) {

                            const dataRole = map(data, item => {
                                if (data1.list.findIndex((item1: any) => item.id === item1.idSubRole) !== -1) {
                                    return {
                                        ...item,
                                        checked: true
                                    }
                                } else {
                                    return {
                                        ...item,
                                        checked: false
                                    }
                                }
                            })

                            setData(dataRole)
                        }
                    },
                    () => { }
                )
            )
        } else {
            setRoleName("")
            const dataRole = map(data, item => {
                return {
                    ...item,
                    checked: false
                }
            })
            setData(dataRole)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [item]);

    useEffect((): void => {
        dispatch(
            getAllSubRoles({},
                (data: any) => {
                    if (data.list.length >= 0) {
                        setData(map(data.list, (item: any) => {
                            return {
                                ...item,
                                checked: false
                            }
                        }))
                    }
                },
                () => { }
            )
        )
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    function handleCreate(e: FormEvent): void {
        e.preventDefault();
        // const dataRole = filter(data, (item: any) => item.checked === true)
        const payloadCreate = map(data, (item: any) => {
            return {
                roleName: roleName,
                idSubRole: item.id,
                RoleCode: item.RoleCode,
                subRoleName: item.subRoleName,
                status: item.checked === true ? 1 : 0
            }
        });

        const payload = {
            data: payloadCreate
        }
        if (!get(item, "original.id")) {
            dispatch(
                createRole(payload,
                    (): void => {
                        onSuccessCreated();
                        toast(
                            <>
                                <i className="fa fa-check-square mr-2" />
                                {t('Tạo quyền')} {t('thành công')}
                            </>,
                            {
                                type: 'success',
                            },
                        );
                    },
                    (): void => {
                        toast(
                            <>
                                <i className="fa fa-window-close mr-2" />
                                {t('Đã có lỗi xảy ra ')}
                            </>,
                            {
                                type: 'error',
                            },
                        );
                    },
                ),
            );
        } else {
            const payloadUpdate = payload.data.map(item1 => {
                return { ...item1, idRole: get(item, "original.id") }
            })
            dispatch(
                updateRole({ data: payloadUpdate },
                    (): void => {
                        onSuccessCreated();
                        toast(
                            <>
                                <i className="fa fa-check-square mr-2" />
                                {t('Cập nhật quyền')} {t('thành công')}
                            </>,
                            {
                                type: 'success',
                            },
                        );
                    },
                    (): void => {
                        toast(
                            <>
                                <i className="fa fa-window-close mr-2" />
                                {t('Đã có lỗi xảy ra ')}
                            </>,
                            {
                                type: 'error',
                            },
                        );
                    },
                ),
            );
        }
    }

    function onChangeRole(item: any): (event: React.FormEvent<HTMLInputElement>) => void {
        return (event: React.FormEvent<HTMLInputElement>): void => {
            const dataRole = map(data, (item1: any) => {
                if (item.id !== item1.id) {
                    return {
                        ...item1
                    }
                }
                return {
                    ...item,
                    checked: !item.checked
                }
            })
            setData(dataRole)
        };
    }

    function onChangeAllRole(checked: boolean): (event: React.MouseEvent) => void {
        return (event: React.MouseEvent): void => {
            const dataRole = map(data, (item: any) => {
                return {
                    ...item,
                    checked: checked
                }
            })
            setData(dataRole)
        };
    }

    function handleChangeTextboxValue(setValueFunction: Function): (event: React.FormEvent<HTMLInputElement>) => void {
        return (event: React.FormEvent<HTMLInputElement>): void => {
            setValueFunction(event.currentTarget.value);
        };
    }

    return (
        <Modal isOpen={visible} className="sipTitleModalCreateNew">
            <ModalHeader toggle={onHide}>{modalTitle}</ModalHeader>
            <ModalBody>
                <FormGroup>
                    <Label>{t('Tên quyền')}</Label>
                    <Input
                        value={roleName}
                        onChange={handleChangeTextboxValue(setRoleName)}
                        placeholder={t('Nhập tên quyền')}
                    />
                </FormGroup>
                <Row className="pl-1 pt-3" style={{ justifyContent: "space-around" }}>
                    <FormGroup>
                        <Button color="primary" onClick={onChangeAllRole(true)}>Chọn tất cả</Button>
                    </FormGroup>
                    <FormGroup>
                        <Button color="primary" onClick={onChangeAllRole(false)}>Bỏ chọn tất cả</Button>
                    </FormGroup>
                </Row>
                <Row className="pl-1 pt-3" style={{ justifyContent: "space-around" }}>
                    {map(data, (item: any) => (
                        <FormGroup key={item.id.toString()}>
                            <Label>{item.subRoleName}</Label>
                            <CustomInput
                                type="switch"
                                id={item.id.toString()}
                                name={item.RoleCode}
                                checked={item.checked}
                                onChange={onChangeRole(item)}
                            />
                        </FormGroup>
                    ))}
                </Row>
            </ModalBody>
            <ModalFooter className="justify-content-end">
                <Button color="primary" onClick={handleCreate}>
                    {t('Ghi lại')}
                </Button>
            </ModalFooter>
        </Modal>
    );
};

export default ModalRole;
